// axios
import request from '@/utils/request.js'

// 

// swiper
export const getRankRead = () => {
  // returnaxiospromise
  return request({
    url: '/test/wbcomic/home/rank_read',
    method: 'GET',
    // 
    params: {
      _type: 'h5'

    }
  })
}
